.App-link {
  color: #61dafb;
}

.Front-img-container > img {
  margin-top: 2rem;
  width: 20%;
  height: auto;
}

@media only screen and (max-width: 780px) {
  .Front-img-container > img {
    width: 80%;
  }
}

.Navbar {
  padding: 1rem;
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: flex-end;
  background-color: #000000;
}

.Navbar > a {
  margin-right: 2rem;
}

.Navbar > a > img {
  width: 32px;
  height: 32px;
}

.Description {
  width: 40%;
  margin: auto;
  margin-top: 2rem;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #000000;
  font-size: 14px;
  line-height: 1.5;
  text-align: justify;
}

@media only screen and (max-width: 780px) {
  .Description {
    width: 90%;
  }
}

.Download > button {
  width: 10%;
  margin: auto;
  margin-top: 2rem;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #000000;
  color: rgb(255, 255, 255);
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
}

@media only screen and (max-width: 780px) {
  .Download > button {
    width: 90%;
  }
}

.Footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5%;
  background-color: #000000;
  color: white;
  padding: 1rem;
  word-wrap: break-word; 
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


