ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    margin-bottom: 5px;
  }
  
  a {
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }

  .Content {
    text-align: center;
  }

  .Card {
    width: 10%;
    float: left;
    padding: 20px;
    border: 2px solid rgb(68, 66, 66);
    color: white;
    text-align: center;
  }

  @media only screen and (max-width: 780px) {
    .Card {
      width: 100%;
    }
  }